import React from 'react';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import { NavLink } from 'react-router-dom';
import { subNavOptions } from '../../routeTemplates';
import path from '../../helpers/path';

import './SubNavRowComponent.css';

const SubNavRowClasses = getBEMClasses('sub-nav-row');

const SubNavRowComponent = (props, context) => (
  <div className={SubNavRowClasses()}>
    {subNavOptions.subNavItems.map(link => (
      <NavLink
        exact
        key={link.path}
        to={path(link.path)}
        className={SubNavRowClasses('item')}
        activeClassName={SubNavRowClasses('item', 'selected')}>
        {context.t(link.text)}
      </NavLink>
    ))}
  </div>
);

SubNavRowComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default SubNavRowComponent;



